require('./scss/styles.scss');
import {SeasonsHeader} from "./js/header";
import debounce from "lodash/debounce";
import $ from 'jquery';
import * as bootstrap from "./js/prevJs/bootstrap.bundle.min";
import * as ClassicEditor from "./js/prevJs/ckeditor";
import 'select2';
import moment from 'moment';
import 'daterangepicker' ;
import {Dropzone} from "dropzone";
import {Chart, registerables} from 'chart.js';
import ApexCharts from 'apexcharts';

Chart.register(...registerables);

const darkMode = localStorage.getItem('darkMode');
const themeClass = darkMode === 'enabled' ? 'dark' : 'light';

// Apply the theme class to the document immediately
document.documentElement.className = themeClass;

document.addEventListener("DOMContentLoaded", (event) => {

        const darkModeToggle = document.getElementById('dark-mode-toggle');
        const lightModeToggle = document.getElementById('light-mode-toggle');

        const toggleMode = (isDarkMode) => {
            document.documentElement.classList.toggle('dark', isDarkMode);
            localStorage.setItem('darkMode', isDarkMode ? 'enabled' : 'disabled');
            updateToggleButtons(isDarkMode);
        };

        const updateToggleButtons = (isDarkMode) => {
            if (isDarkMode) {
                darkModeToggle.classList.remove('activate');
                lightModeToggle.classList.add('activate');
            } else {
                lightModeToggle.classList.remove('activate');
                darkModeToggle.classList.add('activate');
            }
        };

        // Initial activation based on current theme
        updateToggleButtons(themeClass === 'dark');

        // Add event listeners if elements are present
        if (darkModeToggle && lightModeToggle) {
            darkModeToggle.addEventListener('click', () => toggleMode(true));
            lightModeToggle.addEventListener('click', () => toggleMode(false));
        }

        let labelColor=  document.documentElement.className === 'light' ? '#011222' : '#fff';

        const gr = document.getElementById('coursesGraphic');
        if (gr) {
            const ctx = gr.getContext('2d');
            const coursesGraphic = new Chart(ctx, {
                type: 'bar', // Тип графика: вертикальный столбчатый график
                data: {
                    labels: ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December'], // Метки для оси X
                    datasets: [
                        {
                            label: '1 попытка',
                            data: [12, 15, 7, 10, 16, 12, 11, 13, 14, 23, 10, 9], // Данные для категории 1
                            backgroundColor: 'rgba(39, 116, 51, 1)',
                            borderColor: 'rgba(39, 116, 51, 1)',
                            borderWidth: 1
                        },
                        {
                            label: '2 попытки',
                            data: [70, 99, 86, 36, 67, 67, 54, 65, 78, 56, 58, 75], // Данные для категории 2
                            backgroundColor: 'rgba(39, 77, 116, 1)',
                            borderColor: 'rgba(39, 77, 116, 1)',
                            borderWidth: 1
                        },
                        {
                            label: '3 попытки',
                            data: [10, 10, 15, 20, 25, 30, 10, 5, 2, 3, 5, 12], // Данные для категории 3
                            backgroundColor: 'rgba(197, 105, 64, 1)',
                            borderColor: 'rgba(197, 105, 64, 1)',
                            borderWidth: 1
                        },
                        {
                            label: '4 и более',
                            data: [8, 12, 20, 30, 23, 10, 60, 32, 35, 5, 4, 6], // Данные для категории 4
                            backgroundColor: 'rgba(230, 41, 0, 1)',
                            borderColor: 'rgba(230, 41, 0, 1)',
                            borderWidth: 1
                        }
                    ]
                },
                options: {
                    plugins: {
                        legend: {
                            labels: {
                                color: `${labelColor}` // Цвет заголовков категорий
                            }
                        }
                    },
                    scales: {
                        y: {
                            beginAtZero: true // Начало оси Y с нуля
                        }
                    }
                }
            });
        }

        const grAn = document.getElementById('accountsCreated');
        if (grAn) {
            const ctx = grAn.getContext('2d');
            const coursesGraphic = new Chart(ctx, {
                type: 'bar', // Тип графика: вертикальный столбчатый график
                data: {
                    labels: ['Январь', 'Февраль', 'Март', 'Апрель', 'Май', 'Июнь', 'Июль', 'Август', 'Сентябрь', 'Октябрь', 'Ноябрь', 'Декабрь'], // Метки для оси X
                    datasets: [
                        {
                            label: "Аккаунты",
                            data: [12, 15, 7, 10, 16, 12, 11, 13, 14, 23, 10, 9], // Данные для категории 1
                            backgroundColor: '#274D74',
                            borderColor: '#274D74',
                            borderWidth: 1
                        },
                    ]
                },
                options: {
                    plugins: {
                        legend: {
                            labels: {
                                color: `${labelColor}` // Цвет заголовков категорий
                            }
                        }
                    },
                    scales: {
                        y: {
                            beginAtZero: true // Начало оси Y с нуля
                        }
                    }
                }
            });
        }
        const grUsers = document.getElementById('activeUsers');
        if (grUsers) {
            const ctx = grUsers.getContext('2d');
            const coursesGraphic = new Chart(ctx, {
                type: 'bar', // Тип графика: вертикальный столбчатый график
                data: {
                    labels: ['Январь', 'Февраль', 'Март', 'Апрель', 'Май', 'Июнь', 'Июль', 'Август', 'Сентябрь', 'Октябрь', 'Ноябрь', 'Декабрь'], // Метки для оси X
                    datasets: [
                        {
                            label: "Пользователи онлайн",
                            data: [12, 15, 7, 10, 16, 12, 11, 13, 14, 23, 10, 9], // Данные для категории 1
                            backgroundColor: '#C56940',
                            borderColor: '#C56940',
                            borderWidth: 1
                        },
                    ]
                },
                options: {
                    plugins: {
                        legend: {
                            labels: {
                                color: `${labelColor}` // Цвет заголовков категорий
                            }
                        }
                    },
                    scales: {
                        x: {
                            title: {
                                display: true, // Включаем отображение заголовка оси X
                                text: 'Месяцы' // Название оси X
                            }
                        },
                        y: {
                            beginAtZero: true,
                            title: {
                                display: true, // Включаем отображение заголовка оси Y
                                text: 'Значения' // Название оси Y
                            }
                        }
                    }
                }
            });
        }

        const graphic1 = document.getElementById('graphic');
        if (graphic1) {
            const ctx = graphic1.getContext('2d');
            const coursesGraphic = new Chart(ctx, {
                type: 'bar', // Тип графика: вертикальный столбчатый график
                data: {
                    labels: ['Январь', 'Февраль', 'Март', 'Апрель', 'Май', 'Июнь', 'Июль', 'Август', 'Сентябрь', 'Октябрь', 'Ноябрь', 'Декабрь'], // Метки для оси X
                    datasets: [
                        {
                            label: "Пользователи онлайн",
                            data: [12, 15, 7, 10, 16, 12, 11, 13, 14, 23, 10, 9], // Данные для категории 1
                            backgroundColor: '#274D74',
                            borderColor: '#274D74',
                            borderWidth: 1
                        },
                    ]
                },
                options: {
                    plugins: {
                        legend: {
                            labels: {
                                color: `${labelColor}` // Цвет заголовков категорий
                            }
                        }
                    },
                    scales: {
                        x: {
                            title: {
                                display: true, // Включаем отображение заголовка оси X
                                text: 'Месяцы' // Название оси X
                            }
                        },
                        y: {
                            beginAtZero: true,
                            title: {
                                display: true, // Включаем отображение заголовка оси Y
                                text: 'Значения' // Название оси Y
                            }
                        }
                    }
                }
            });
        }
        const graphic2 = document.getElementById('graphic2');
        if (graphic2) {
            const ctx = graphic2.getContext('2d');
            const coursesGraphic = new Chart(ctx, {
                type: 'bar', // Тип графика: вертикальный столбчатый график
                data: {
                    labels: ['Январь', 'Февраль', 'Март', 'Апрель', 'Май', 'Июнь', 'Июль', 'Август', 'Сентябрь', 'Октябрь', 'Ноябрь', 'Декабрь'], // Метки для оси X
                    datasets: [
                        {
                            label: "Пользователи онлайн",
                            data: [12, 15, 7, 10, 16, 12, 11, 13, 14, 23, 10, 9], // Данные для категории 1
                            backgroundColor: '#274D74',
                            borderColor: '#274D74',
                            borderWidth: 1
                        },
                    ]
                },
                options: {
                    plugins: {
                        legend: {
                            labels: {
                                color: `${labelColor}` // Цвет заголовков категорий
                            }
                        }
                    },
                    scales: {
                        x: {
                            title: {
                                display: true, // Включаем отображение заголовка оси X
                                text: 'Месяцы' // Название оси X
                            }
                        },
                        y: {
                            beginAtZero: true,
                            title: {
                                display: true, // Включаем отображение заголовка оси Y
                                text: 'Значения' // Название оси Y
                            }
                        }
                    }
                }
            });
        }

        if (document.getElementById('earnigs_chart')) {
            var options = {
                series: [{
                    name: "Earnings",
                    data: [25, 40, 30, 55, 25, 35, 25, 50, 20, 40, 20, 50]
                }],
                chart: {
                    height: 273,
                    type: 'area',
                    zoom: {
                        enabled: false
                    }
                },
                colors: ['#FF4667'],
                dataLabels: {
                    enabled: false
                },
                stroke: {
                    curve: 'straight'
                },
                title: {
                    text: '',
                    align: 'left'
                },
                xaxis: {
                    categories: ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'],
                },
                yaxis: {
                    min: 10,
                    max: 60,
                    tickAmount: 5,
                    labels: {
                        formatter: (val) => {
                            return val / 1 + 'K';
                        }
                    }
                },
                legend: {
                    position: 'top',
                    horizontalAlign: 'left'
                }
            };

            // Создаем график с использованием ApexCharts
            var chart = new ApexCharts(document.querySelector("#earnigs_chart"), options);
            chart.render();
        }

        const header = document.querySelector('.header');
        new SeasonsHeader(header);

        const dropzoneEls = document.querySelectorAll('.dropzone');
        if (dropzoneEls.length) {
            dropzoneEls.forEach(dropzoneEl => {
                new Dropzone(dropzoneEl, {});
            });
        }


        if ($('.bookingrange').length > 0) {
            var start = moment().subtract(6, 'days');
            var end = moment();

            function booking_range(start, end) {
                $('.bookingrange span').html(start.format('M/D/YYYY') + ' - ' + end.format('M/D/YYYY'));
            }

            $('.bookingrange').daterangepicker({
                startDate: start,
                endDate: end,
                ranges: {
                    'Today': [moment(), moment()],
                    'Yesterday': [moment().subtract(1, 'days'), moment().subtract(1, 'days')],
                    'Last 7 Days': [moment().subtract(6, 'days'), moment()],
                    'Last 30 Days': [moment().subtract(29, 'days'), moment()],
                    'This Month': [moment().startOf('month'), moment().endOf('month')],
                    'Last Month': [moment().subtract(1, 'month').startOf('month'), moment().subtract(1, 'month').endOf('month')]
                }
            }, booking_range);

            booking_range(start, end);
        }


        if ($('#editor').length > 0) {
            ClassicEditor
                .create(document.querySelector('#editor'), {
                    toolbar: {
                        items: [
                            'heading', '|',
                            'fontfamily', 'fontsize', '|',
                            'alignment', '|',
                            'fontColor', 'fontBackgroundColor', '|',
                            'bold', 'italic', 'strikethrough', 'underline', 'subscript', 'superscript', '|',
                            'link', '|',
                            'outdent', 'indent', '|',
                            'bulletedList', 'numberedList', 'todoList', '|',
                            'code', 'codeBlock', '|',
                            'insertTable', '|',
                            'uploadImage', 'blockQuote', '|',
                            'undo', 'redo'
                        ],
                        shouldNotGroupWhenFull: true
                    }
                })
                .then(editor => {
                    window.editor = editor;
                })
                .catch(err => {
                    console.error(err.stack);
                });
        }

        if ($('.select').length > 0) {
            $('.select').select2({
                minimumResultsForSearch: -1,
                width: '100%'
            });
        }

        $(document).ready(function () {
            let progressVal = 0;
            let businessType = 0;

            $(".next_btn").click(function () {
                $(this).parent().parent().parent().next().fadeIn('slow');
                $(this).parent().parent().parent().css({
                    'display': 'none'
                });
                progressVal = progressVal + 1;
                $('.progress-active').removeClass('progress-active').addClass('progress-activated').next().addClass('progress-active');
            });

            $(".prev_btn").click(function () {
                $(this).parent().parent().parent().prev().fadeIn('slow');
                $(this).parent().parent().parent().css({
                    'display': 'none'
                });
                progressVal = progressVal - 1;
                $('.progress-active').removeClass('progress-active').prev().removeClass('progress-activated').addClass('progress-active');
            });
        });


        function previewImage(event) {
            const input = event.target;
            const elId = input.getAttribute('id');
            if (elId) {
                const preview = document.querySelector(`[data-preview-id=${elId}]`);
                if (preview) {
                    preview.innerHTML = ''; // Очистить предыдущий контент

                    if (input.files && input.files[0]) {
                        const reader = new FileReader();

                        reader.onload = function (e) {
                            const img = document.createElement('img');
                            img.src = e.target.result;
                            preview.appendChild(img);
                        }

                        reader.readAsDataURL(input.files[0]);
                    }
                }
            }
        }

        const inputsWithPhoto = document.querySelectorAll('[data-input-id]');
        if (inputsWithPhoto.length) {
            inputsWithPhoto.forEach(inputWithPhoto => {
                inputWithPhoto.addEventListener('change', previewImage);
            });
        }


        const doProportionIcon = (icons) => {
            for (const icon of icons) {
                const widthAttr = icon.getAttribute('width');
                const widthReal = icon.clientWidth;
                const heightAttr = icon.getAttribute('height');
                const heightReal = (widthReal / widthAttr) * heightAttr;
                icon.style.height = `${heightReal}px`;
            }
        }


        const icons = document.querySelectorAll('.icon-pr');
        if (icons.length) {
            doProportionIcon(icons);
            window.addEventListener('resize', debounce(() => {
                doProportionIcon(icons);
            }, 300));
        }

        const settingsBoxes = document.querySelectorAll('.created-course-row__settings-col');
        if (settingsBoxes.length > 0) {
            const clickOutSettingBox = ({target}) => {
                if (!target.classList.contains('created-course-row__settings') && !target.closest('.created-course-row__settings')) {
                    const openSettingsBox = document.querySelector('.created-course-row__settings[data-open]')
                    if (openSettingsBox) {
                        openSettingsBox.removeAttribute('data-open');
                    }
                    document.removeEventListener('click', clickOutSettingBox);
                }
            }
            settingsBoxes.forEach(settingsBox => {
                const btn = settingsBox.querySelector('.created-course-row__settings-btn');
                const settings = settingsBox.querySelector('.created-course-row__settings');
                if (btn && settings) {
                    btn.addEventListener('click', (evt) => {
                        evt.stopImmediatePropagation();
                        if (settings.hasAttribute('data-open')) {
                            settings.removeAttribute('data-open')
                            document.removeEventListener('click', clickOutSettingBox);
                        } else {
                            settingsBoxes.forEach(otherBox => {
                                const otherSettingBox = otherBox.querySelector('.created-course-row__settings');
                                if (otherSettingBox !== settings && otherSettingBox.hasAttribute('data-open')) {
                                    otherSettingBox.removeAttribute('data-open')
                                }
                            });
                            settings.setAttribute('data-open', true);
                            document.addEventListener('click', clickOutSettingBox);
                        }
                    });
                }
            });
        }


    }
);